.footer-social-networks__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.footer-social-networks__item {
  margin-right: 11px;

  &:last-child {
    margin: 0;
  }
}

.footer-social-networks__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  background-color: $color-default-white;
  border-radius: 50%;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }

  &:focus {
    outline: 2px solid $color-default-white;
    border-radius: 50%;
  }
}

.footer-social-networks__link svg {
  width: 20px;
  height: 20px;
}
